import { STROKE } from "../../constants.js";
import { cloud } from "../elements/cloud.js";
import { snow } from "../elements/snow.js";
import { sun } from "../elements/sun.js";
export function iconSnowDay(ctx, time, color) {
    const width = ctx.canvas.width;
    const height = ctx.canvas.height;
    const shorter = Math.min(width, height);
    sun(ctx, time, width * 0.625, height * 0.325, shorter * 0.65, shorter * STROKE * 0.8, color.sun);
    snow(ctx, time, width * 0.375, height * 0.5, shorter * 0.7, shorter * STROKE * 0.9, color.snow);
    cloud(ctx, time, width * 0.375, height * 0.5, shorter * 0.7, shorter * STROKE * 0.9, color.cloud);
}
