import { STROKE } from "../../constants.js";
import { cloud } from "../elements/cloud.js";
import { hail } from "../elements/hail.js";
export function iconHail(ctx, time, color) {
    const width = ctx.canvas.width;
    const height = ctx.canvas.height;
    const shorter = Math.min(width, height);
    hail(ctx, time, width * 0.5, height * 0.37, shorter * 0.9, shorter * STROKE, color.rain);
    cloud(ctx, time, width * 0.5, height * 0.37, shorter * 0.9, shorter * STROKE, color.cloud);
}
